import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { AVAILABLE_ASSETS, BlockPopup, RPC_URL_BY_CHAIN, EXPLORER_TXN_URL_LIST, ToastPopup, addressRegex } from '../../actions/constant'
import { getCoinBalance, getMarketPrice, getTokenBalance, sendCoin, sendTokenFromContract } from '../../actions/common'
import truncateEthAddress from 'truncate-eth-address'

const SendToken = ({ getBalanceDashboard }) => {

    const { chainId, signer, provider } = useSelector(state => state.web3)
    const { user } = useSelector(state => state.auth)


    const [selectedasset, setselectedasset] = useState(null)
    const [selectedtype, setselectedtype] = useState(null)
    const [filterAsset, setfilterAsset] = useState([])
    const [assetBalance, setAssetBalance] = useState(0)
    const [currentPrice, setcurrentPrice] = useState(0)
    const [balloading, setballoading] = useState(false)

    //Sent Token Forms
    const [receiverAddress, setreceiverAddress] = useState("0x3d7c8e92dc84c2d0d36e7545d3c39b1464d86e95")
    const [sendToken, setSendToken] = useState("10")

    //
    const [txnStatus, settxnStatus] = useState({})





    useEffect(() => {
        filterAssets()
    }, [])

    useEffect(() => {
        settxnStatus({})
    }, [sendToken, receiverAddress, selectedasset])

    useEffect(() => {

        if (selectedasset) {
            getBalance()
            const findType = AVAILABLE_ASSETS.find(x => x.value === selectedasset && x.networkID === parseInt(chainId))
            setselectedtype(findType.type)
        }

    }, [selectedasset])

    const getBalance = async () => {
        setAssetBalance(0)
        setcurrentPrice(0)
        setballoading(true)
        let findasset = AVAILABLE_ASSETS.find(x => x.value === selectedasset && x.networkID === parseInt(chainId))
        if (findasset && findasset.type === "coin") {
            let getBal = await getCoinBalance(signer)
            let currentprice = await getMarketPrice(findasset.ids)
            setAssetBalance(getBal)
            setcurrentPrice(currentprice)
            setballoading(false)
        } else if (findasset && findasset.type === "token") {
            let getBal = await getTokenBalance(findasset.contractAddress, user.walletAddress, signer)
            let currentprice = await getMarketPrice(findasset.ids)
            setAssetBalance(getBal)
            setcurrentPrice(currentprice)
            setballoading(false)

        }
    }


    const filterAssets = () => {
        const filterValue = AVAILABLE_ASSETS.filter(x => x.networkID === parseInt(chainId))

        if (filterValue.length > 0) {
            console.log("filterValue", filterValue[0].type)
            setselectedasset(filterValue[0].value)
            setselectedtype(filterValue[0].type)
            setfilterAsset(filterValue)
        }
    }

    var filterAssetRender = filterAsset.map((result, index) => {
        return <option value={result.value}>{result.label}</option>
    })

    var alertRender;

    console.log("selected type", selectedtype)

    const triggerSend = async (e) => {
        e.preventDefault()
        if (receiverAddress === "" || sendToken === "") {
            ToastPopup.failure("Fill All the Fields")
            return
        }
        // Regular expression pattern to match Ethereum wallet address
        if (!addressRegex.test(receiverAddress)) {
            ToastPopup.failure("Invalid Address.Address start with 0x")
            return
        }
        if (parseFloat(sendToken) <= 0) {
            ToastPopup.failure("Send token must be greater the zero")
            return
        }

        if (parseFloat(sendToken) > parseFloat(assetBalance)) {
            ToastPopup.failure("Low Balance")
            return
        }
        console.log("token", selectedtype)


        BlockPopup.standard('.sendtoken', `Please wait... Don't Close`);
        if (selectedtype === "coin") {
            const sendResult = await sendCoin(provider, signer, receiverAddress, sendToken)
            console.log("sendResult", sendResult)
            settxnStatus(sendResult)
            getBalance()
            getBalanceDashboard()
            BlockPopup.remove('.sendtoken');

        } else if (selectedtype === "token") {
            let findasset = AVAILABLE_ASSETS.find(x => x.value === selectedasset && x.networkID === parseInt(chainId))
            const sendResult = await sendTokenFromContract(provider, signer, findasset.contractAddress, receiverAddress, sendToken)
            console.log("sendResult", sendResult)
            settxnStatus(sendResult)
            getBalance()
            getBalanceDashboard()
            console.log("SUBMIT RESULT", sendResult)
            BlockPopup.remove('.sendtoken');
        }
    }





    return (
        <div>
            <>

                <div
                    id="sendModal"
                    className="modal fade "
                    tabIndex={-1}
                    role="dialog"
                    aria-labelledby="myModalLabel"
                    aria-hidden="true"
                >

                    <div className="modal-dialog">

                        <div className="modal-content sendtoken">
                            <div className="modal-body">

                                <div>
                                    <div>
                                        <div className="row g-0">
                                            <div className="col-md-12">
                                                <div className="p-xl-5 p-3">

                                                    <h3 className="h3 mb-0 mt-3 text-center">Send Token</h3>
                                                    <p className="text-muted mt-1 mb-4 text-center">
                                                        Enter your Valid Wallet Address (XRC20) otherwise you will lose your fund.
                                                    </p>
                                                    {/*form start*/}
                                                    <form onSubmit={triggerSend} className="authentication-form">
                                                        <div className="mb-3">
                                                            <label htmlFor="exampleInputEmail1" className="form-label">
                                                                Wallet Address <small>*</small>
                                                            </label>
                                                            <input
                                                                type="text"
                                                                className="form-control"
                                                                id="exampleInputEmail1"
                                                                placeholder="0x......"
                                                                onChange={(e) => setreceiverAddress(e.target.value)}
                                                                value={receiverAddress}
                                                            />
                                                        </div>
                                                        <div className="mb-3">
                                                            <label htmlFor="exampleInputEmail1" className="form-label">
                                                                Number of Tokens to Send <small>*</small>
                                                            </label>
                                                            <input
                                                                type="number"

                                                                className="form-control"
                                                                id="exampleInputEmail1"
                                                                placeholder="10"
                                                                onChange={(e) => setSendToken(e.target.value)}
                                                                value={sendToken}

                                                            />
                                                        </div>
                                                        <div className="mb-3">
                                                            <div className="row">
                                                                <div className="col">
                                                                    <ul className="checkout-list">
                                                                        <li>
                                                                            <span>Select Asset</span>
                                                                            <span>
                                                                                <select value={selectedasset} onChange={(e) => setselectedasset(e.target.value)} className='form-select'>
                                                                                    {filterAssetRender}
                                                                                </select>
                                                                            </span>
                                                                        </li>
                                                                        <li>
                                                                            <span>Your Balance</span>
                                                                            <span className="checkout-total">{parseFloat(assetBalance).toFixed(4)} {selectedasset}</span>
                                                                        </li>
                                                                        <li>
                                                                            <span>{selectedasset} Current Price</span>
                                                                            <span className="checkout-balance text-primary">{currentPrice} USD</span>
                                                                        </li>


                                                                    </ul>
                                                                </div>
                                                            </div>

                                                        </div>
                                                        {txnStatus && txnStatus.status && Object.keys(txnStatus).length > 0 && <div role="alert" class="alert text-success bg-white border alert-dismissible">
                                                            <div class="d-flex align-items-start">
                                                                <span class="badge badge-soft-success align-self-center me-3">Success</span>
                                                                <div class="w-100">Your transaction is success <a href={EXPLORER_TXN_URL_LIST[chainId] + txnStatus.txhash} target="_blank" class="text-success alert-link">View Transaction</a></div>
                                                            </div>
                                                            <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
                                                        </div>}
                                                        {(txnStatus && Object.keys(txnStatus).length > 0 && !txnStatus.status) && <div role="alert" class="alert text-danger bg-white border alert-dismissible">
                                                            <div class="d-flex align-items-start">
                                                                <span class="badge badge-soft-danger align-self-center me-3">Ohh no!</span>
                                                                <div class="w-100">Your transaction is failed <a href={EXPLORER_TXN_URL_LIST[chainId] + txnStatus.txhash} target="_blank" class="text-danger alert-link">View Transaction</a></div>
                                                            </div>
                                                            <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
                                                        </div>}


                                                        <div className="mb-0 text-center d-grid">
                                                            {!balloading ? <button className="btn btn-primary" type="submit">
                                                                Send
                                                            </button> : <button class="btn btn-outline-primary btn-sm" type="button">
                                                                <span class="spinner-border spinner-border-sm me-1" role="status" aria-hidden="true"></span>
                                                                Fetching details..
                                                            </button>}
                                                        </div>
                                                    </form>

                                                </div>
                                            </div>

                                        </div>
                                    </div>{" "}
                                    {/* end card-body */}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>

        </div>
    )
}

export default SendToken
