import React, { useEffect, useRef } from 'react';
import QRCode from 'qrcode';

const QRCodeGenerator = ({ tokenAddress, tokenAmount, label, message, size }) => {
    const qrCodeRef = useRef(null);

    useEffect(() => {
        const generateQRCode = async () => {
            try {
                const link = `ethereum:${tokenAddress}?label=${encodeURIComponent(
                    label
                )}&message=${encodeURIComponent(message)}`;
                const canvas = qrCodeRef.current;
                await QRCode.toCanvas(canvas, link, { width: size, height: size });
                console.log('QR code generated!');
            } catch (error) {
                console.error('Error generating QR code:', error);
            }
        };

        generateQRCode();
    }, [tokenAddress, tokenAmount, label, message, size]);

    return <canvas ref={qrCodeRef} />;
};

export default QRCodeGenerator;
