import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import BaseLayout from '../commonlayout/BaseLayout';
import { copyTextToClipboard, getCoinBalance, getMarketPrice, getTokenBalance, getTxnStatus } from '../../actions/common';
import truncateEthAddress from 'truncate-eth-address'
import { setBalance } from '../../reducers/web3Reducer';
import SendToken from './SendToken';
import ReceiveToken from './ReceiveToken';
import { API_URL, AVAILABLE_ASSETS, EXPLORER_TXN_URL_LIST, EXPLORER_URL_LIST, RPC_URL_BY_CHAIN, TXN_API_URL_LIST, WALLET_URL_LIST } from '../../actions/constant';
import axios from 'axios';
import CustomToken from './CustomToken';

const Dashboard = () => {
    const { user } = useSelector(state => state.auth)
    const dispatch = useDispatch()
    const { connected, providerConnect, provider, signer, balance, symbol, chainId } = useSelector(state => state.web3)

    const [tokenList, settokenList] = useState([])
    const [customtokenList, setcustomtokenList] = useState([])
    const [TokenRender, setTokenRender] = useState([])
    const [CustomTokenRender, setCustomTokenRender] = useState([])

    const [TxnRender, setTxnRender] = useState([])
    const [txnLoading, settxnLoading] = useState(false)

    useEffect(() => {
        if (connected) {
            getBalance()
            getCustomToken()
        }

    }, [connected])
    const getBalance = async () => {
        if (connected && signer) {
            let balance = await getCoinBalance(signer)
            dispatch(setBalance(parseFloat(balance).toFixed(4)))
            const filterValue = AVAILABLE_ASSETS.filter(x => x.networkID === parseInt(chainId) && x.type === "token")
            settokenList(filterValue)

        }
    }

    const getCustomToken = async () => {
        try {
            let result = await axios.get(API_URL + "/api/customtoken")
            setcustomtokenList(result.data)
        } catch (err) {
            setcustomtokenList([])
        }
    }


    useEffect(() => {
        async function fetchCustomTokenData() {
            const promises = customtokenList.map(async (result) => {
                const balance = await getTokenBalance(result.contractAddress, user.walletAddress, signer);
                return { ...result, balance };
            });

            const resolvedTokens = await Promise.all(promises);
            setCustomTokenRender(resolvedTokens);
        }

        fetchCustomTokenData();
    }, [customtokenList]);

    useEffect(() => {
        async function fetchTokenData() {
            const promises = tokenList.map(async (result) => {
                const balance = await getTokenBalance(result.contractAddress, user.walletAddress, signer);
                const currentPrice = await getMarketPrice(result.ids);

                return { ...result, balance, currentPrice };
            });

            const resolvedTokens = await Promise.all(promises);
            setTokenRender(resolvedTokens);
        }

        fetchTokenData();
    }, [tokenList]);

    const fetchTransaction = async () => {
        settxnLoading(true)
        const url = `${TXN_API_URL_LIST[chainId]}/${user.walletAddress}?page=1&limit=10&tx_type=all`;
        const response = await axios.get(url);

        if (response.data && response.data.items) {
            const promises = response.data.items.map(async (result) => {
                const [txHash, status] = await getTxnStatus(result.hash, provider);
                return { txHash, status: status ? "success" : "failure", color: status ? "success" : "danger" };
            });

            const resolvedTxn = await Promise.all(promises);

            setTxnRender(resolvedTxn);
            settxnLoading(false)
        }

    }

    useEffect(() => {

        fetchTransaction();
    }, [balance]);

    var TxnListRender = TxnRender.map(result => {
        return <div className="row justify-content-sm-between mt-4">
            <div className="col-lg-6">

                <a href={`${EXPLORER_TXN_URL_LIST[chainId]}${result.txHash}`} target="_blank">  {result.txHash && truncateEthAddress(result.txHash)}</a> {""}


                <span style={{ cursor: "pointer" }} onClick={() => {
                    copyTextToClipboard(result.txHash)
                }}>
                    <img
                        src="assets/images/icons/copy.svg"
                        className="align-self-center"
                        alt="logo-img"
                        height={16}
                    />
                </span>
                {/* end checkbox */}
            </div>{" "}
            {/* end col */}

            <div className="col-lg-6">
                <ul
                    className="list-inline text-sm-end mb-0"
                    id="tooltip-container2"
                >

                    <li className="list-inline-item">
                        <span className={`badge badge-soft-${result.color}  p-1"`}>
                            {result.status}
                        </span>
                    </li>
                </ul>
            </div>{" "}
            {/* end col */}
        </div>
    })


    return (
        <BaseLayout>
            <section className="position-relative overflow-hidden bg-gradient2 py-3 px-3">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="page-title">
                                <h3 className="mb-0">Hi {user.firstName}</h3>
                                <p className="mt-1 fw-medium">Welcome to XiPay!</p>
                            </div>
                        </div>
                    </div>
                    <div className="row mt-2">
                        {/* profile widget star */}
                        <div className="col-lg-5">
                            <div className="card">
                                <div className="card-body">
                                    <div className="row">
                                        <div className="col">
                                            <div className="d-flex">
                                                <img
                                                    src="https://xinfin.org/assets/images/brand-assets/xdc-icon.png"
                                                    className="img-fluid avatar-sm rounded-sm me-3"
                                                    alt="..."
                                                />
                                                <div className="flex-grow-1">
                                                    <h4 className="mb-1 mt-0 fs-16">{user.walletAddress && truncateEthAddress(user.walletAddress)}
                                                        <span style={{ cursor: "pointer" }} onClick={() => {
                                                            copyTextToClipboard(user.walletAddress)
                                                        }}>
                                                            <i data-feather="copy"></i>
                                                        </span>
                                                    </h4>
                                                    <h2>{balance} {symbol}</h2>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-auto text-end">
                                            <div className="dropdown">
                                                <a
                                                    className="btn-link text-muted dropdown-toggle arrow-none"
                                                    href="#"
                                                    role="button"
                                                    id="dropdownMenuLink-1"
                                                    data-bs-toggle="dropdown"
                                                    aria-haspopup="true"
                                                    aria-expanded="false"
                                                >
                                                    <i
                                                        className="icon icon-xs"
                                                        data-feather="more-horizontal"
                                                    />
                                                </a>
                                                <div
                                                    className="dropdown-menu dropdown-menu-end"
                                                    aria-labelledby="dropdownMenuLink-1"
                                                >
                                                    <a className="dropdown-item" href="#" data-bs-toggle="modal" data-bs-target="#sendModal">
                                                        <i className="icon-xxs icon me-2" data-feather="send" />
                                                        Send
                                                    </a>
                                                    <a className="dropdown-item" href="#" data-bs-toggle="modal" data-bs-target="#receiveModal">
                                                        <i
                                                            className="icon-xxs icon me-2"
                                                            data-feather="download"
                                                        />
                                                        Receive
                                                    </a>
                                                    <div className="dropdown-divider" />
                                                    <a className="dropdown-item text-primary" target='_blank' href={`${WALLET_URL_LIST[chainId]}/${user.walletAddress}`}>
                                                        <i
                                                            className="icon-xxs icon me-2"
                                                            data-feather="eye"
                                                        />
                                                        View on Explorer
                                                    </a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="row align-items-center justify-content-center pt-1">

                                        <button type="button" class="btn btn-soft-primary col-5 me-2" data-bs-toggle="modal" data-bs-target="#sendModal"><span class="pln">Send</span></button>
                                        {/* </div> */}
                                        {/* <div className="col-md-4"> */}
                                        <button type="button" class="btn btn-soft-success col-5 me-2" data-bs-toggle="modal" data-bs-target="#receiveModal"><span class="pln">Receive</span></button>


                                        {/* </div> */}
                                    </div>

                                    <div className="row mt-4">
                                        <div className="col">
                                            <ul className="list-group custom-list-item ">
                                                {
                                                    TokenRender.map(result => {
                                                        return <li className="list-group-item">
                                                            <div className="d-flex align-items-center">
                                                                <img width="32" style={{ borderRadius: 50 }} src={result.icon} alt="Logo" className="logo" />
                                                                <div className="ms-3">
                                                                    <h5 className="mb-0 item-name">{result.name} ({result.value})</h5>
                                                                </div>
                                                                <div className="ms-auto">
                                                                    <h5 class="mt-0 mb-0 text-right">{result.balance}</h5>
                                                                    <h6 className="mb-0 item-details text-secondary text-right">{result.currentPrice} USD</h6>
                                                                </div>
                                                            </div>
                                                        </li>
                                                    })
                                                }

                                                {
                                                    CustomTokenRender.map(result => {
                                                        return <li className="list-group-item">
                                                            <div className="d-flex align-items-center">
                                                                <img width="32" style={{ borderRadius: 50 }} src={"https://ui-avatars.com/api/?background=244b81&color=fff&name=" + result.symbol} alt="Logo" className="logo" />
                                                                <div className="ms-3">
                                                                    <h5 className="mb-0 item-name">{result.name} ({result.symbol})</h5>
                                                                </div>
                                                                <div className="ms-auto">
                                                                    <h5 class="mt-0 mb-0 text-right">{result.balance}</h5>
                                                                    <h6 className="mb-0 item-details text-secondary text-right">~ USD</h6>
                                                                </div>
                                                            </div>
                                                        </li>
                                                    })
                                                }


                                            </ul>
                                            <div class="text-center">
                                                <button data-bs-toggle="modal" data-bs-target="#customtokenModal" class="btn btn-soft-danger btn-sm" type="button">
                                                    <i data-feather="plus-circle" className='mx-1' />
                                                    Add Custom Token
                                                </button>
                                            </div>
                                        </div>
                                    </div>



                                </div>
                            </div>
                        </div>
                        {/* profile widget end */}
                        {/* stats start */}
                        <div className="col-lg-3">
                            <div>
                                <div className="row d-flex align-items-center my-2 pb-2">
                                    <img src="assets/images/ad-place.png" />
                                </div>
                            </div>
                            <div className="card">
                                <div className="card-body">
                                    <div className="d-flex align-items-center">
                                        <div className="avatar-sm icon icon-with-bg icon-xs rounded-sm bg-soft-success me-3">
                                            <i className="icon-dual-success" data-feather="file" />
                                        </div>
                                        <div className="flex-grow-1">
                                            <h3 className="mt-0 mb-0">21</h3>
                                            <p className="text-muted mb-0">Invoice Created</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="card">
                                <div className="card-body">
                                    <div className="d-flex align-items-center">
                                        <div className="avatar-sm icon icon-with-bg icon-xs rounded-sm bg-soft-info me-3 flex-shrink-0">
                                            <i className="icon-dual-info" data-feather="camera" />
                                        </div>
                                        <div className="flex-grow-1">
                                            <h3 className="mt-0 mb-0">21</h3>
                                            <p className="text-muted mb-0">QR Payments</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="card">
                                <div className="card-body">
                                    <div className="d-flex align-items-center">
                                        <div className="avatar-sm icon icon-with-bg icon-xs rounded-sm bg-soft-info me-3 flex-shrink-0">
                                            <i className="icon-dual-primary" data-feather="shopping-bag" />
                                        </div>
                                        <div className="flex-grow-1">
                                            <h3 className="mt-0 mb-0">10</h3>
                                            <p className="text-muted mb-0">Web Payments</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* stats end */}
                        {/* revenue start */}
                        <div className="col-lg-4">


                            <div className="row mb-2">
                                <div className="card">
                                    <div className="card-body">
                                        <div className="row">
                                            <div class="row">
                                                <div class="col">
                                                    <h4 class="mb-3 mt-0 fs-16">Transactions {""}
                                                        {!txnLoading ? <span style={{ cursor: "pointer" }} onClick={() => {
                                                            fetchTransaction()
                                                        }}>
                                                            <img
                                                                src="assets/images/icons/refresh.svg"
                                                                className="align-self-center"
                                                                alt="logo-img"
                                                                height={16}
                                                            />

                                                        </span> : <span class="spinner-border spinner-border-sm me-1" role="status" aria-hidden="true"></span>}
                                                    </h4>
                                                </div>
                                                <div class="col-auto text-end">
                                                    <a href={`${WALLET_URL_LIST[chainId]}/${user.walletAddress}`} target="_blank" class="fw-semibold text-primary fs-13">View All <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-arrow-right ms-1 icon-xxs"><line x1="5" y1="12" x2="19" y2="12"></line><polyline points="12 5 19 12 12 19"></polyline></svg></a>
                                                </div>
                                            </div>

                                            {
                                                TxnListRender
                                            }




                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>
                        {/* revenue end */}
                    </div>

                </div>
            </section>
            <SendToken getBalanceDashboard={getBalance} />
            <ReceiveToken />
            <CustomToken />
        </BaseLayout>
    )
}

export default Dashboard
