import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { AVAILABLE_ASSETS, BlockPopup, RPC_URL_BY_CHAIN, EXPLORER_TXN_URL_LIST, ToastPopup, addressRegex, API_URL } from '../../actions/constant'
import { getCoinBalance, getMarketPrice, getTokenBalance, getTokenSymbol, sendCoin, sendTokenFromContract } from '../../actions/common'
import truncateEthAddress from 'truncate-eth-address'
import TextField from '../common/TextField'
import axios from 'axios'

const CustomToken = ({ getBalanceDashboard }) => {

    const { chainId, signer, provider } = useSelector(state => state.web3)
    const { user } = useSelector(state => state.auth)

    const [loading, setloading] = useState("")

    //Sent Token Forms

    const [contractAddress, setcontractAddress] = useState("")
    const [symbol, setsymbol] = useState("")
    const [name, setname] = useState("")
    const [errors, seterrors] = useState({})



    useEffect(() => {

        const getSymbol = async () => {
            if (contractAddress !== "" && addressRegex.test(contractAddress)) {
                const getSymbolValue = await getTokenSymbol(contractAddress, signer)
                console.log("getSymbolValue", getSymbolValue)
                setsymbol(getSymbolValue)
            }
        }

        getSymbol()


    }, [contractAddress])




    const triggerAddToken = async (e) => {
        e.preventDefault()
        if (contractAddress === "" || symbol === "") {
            ToastPopup.failure("Fill All the Fields")
            return
        }
        // Regular expression pattern to match Ethereum wallet address
        if (!addressRegex.test(contractAddress)) {
            ToastPopup.failure("Invalid Contract Address.Address start with 0x")
            return
        }
        BlockPopup.standard('.addcustomtoken', `Please wait... Don't Close`);

        try {
            const insertData = {
                contractAddress,
                symbol,
                name,
                decimal: "18"
            }
            let result = await axios.post(API_URL + "/api/customtoken", insertData)
            if (result.data) {
                setcontractAddress("")
                setsymbol("")
                setname("")
                BlockPopup.remove('.addcustomtoken');
                ToastPopup.success("Successfully Added")
            }
        } catch (err) {
            BlockPopup.remove('.addcustomtoken');
            if (err && err.response && err.response.data) {
                seterrors(err.response.data)
            }
        }





    }





    return (
        <div>
            <>

                <div
                    id="customtokenModal"
                    className="modal fade "
                    tabIndex={-1}
                    role="dialog"
                    aria-labelledby="myModalLabel"
                    aria-hidden="true"
                >

                    <div className="modal-dialog">

                        <div className="modal-content addcustomtoken">
                            <div className="modal-body">

                                <div>
                                    <div>
                                        <div className="row g-0">
                                            <div className="col-md-12">
                                                <div className="p-xl-5 p-3">

                                                    <h3 className="h3 mb-0 mt-3 text-center">Add Custom Token</h3>
                                                    <p className="text-muted mt-1 mb-4 text-center">
                                                        Enter your Valid Contract Address to add custome token.
                                                    </p>
                                                    {/*form start*/}
                                                    <form onSubmit={triggerAddToken} className="authentication-form">
                                                        <div className="mb-3">
                                                            <TextField
                                                                isRequired
                                                                label="Token Name (for your Reference)"
                                                                onChange={(e) => setname(e.target.value)}
                                                                value={name}
                                                                placeholder=""
                                                                error={errors.symbol}
                                                            />

                                                        </div>
                                                        <div className="mb-3">

                                                            <TextField
                                                                isRequired
                                                                label="Contract Address"
                                                                onChange={(e) => setcontractAddress(e.target.value)}
                                                                value={contractAddress}
                                                                placeholder="0x......"
                                                                error={errors.contractAddress}
                                                            />

                                                        </div>
                                                        <div className="mb-3">
                                                            <TextField
                                                                isRequired
                                                                label="Symbol"
                                                                onChange={(e) => setsymbol(e.target.value)}
                                                                value={symbol}
                                                                placeholder=""
                                                                error={errors.symbol}
                                                            />

                                                        </div>




                                                        <div className="mb-0 text-center d-grid">
                                                            {!loading ? <button className="btn btn-primary" type="submit">
                                                                Add Token
                                                            </button> : <button class="btn btn-outline-primary btn-sm" type="button">
                                                                <span class="spinner-border spinner-border-sm me-1" role="status" aria-hidden="true"></span>
                                                                Please Wait..
                                                            </button>}
                                                        </div>
                                                    </form>

                                                </div>
                                            </div>

                                        </div>
                                    </div>{" "}
                                    {/* end card-body */}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>

        </div>
    )
}

export default CustomToken
