import React from 'react'
import QRCodeGenerator from '../common/QRCodeGenerator'
import { useSelector } from 'react-redux'

const ReceiveToken = () => {
    const { user } = useSelector(state => state.auth)
    return (
        <div>
            <>

                <div
                    id="receiveModal"
                    className="modal fade"
                    tabIndex={-1}
                    role="dialog"
                    aria-labelledby="myModalLabel"
                    aria-hidden="true"
                >

                    <div className="modal-dialog">

                        <div className="modal-content">
                            <div className="modal-body">

                                <div>
                                    <div>
                                        <div className="row g-0">
                                            <div className="col-md-12">
                                                <div className="p-xl-5 p-3">


                                                    <h3 className="h3 mb-0 mt-3 text-center">Receive Token</h3>
                                                    <p className="text-muted mt-1 mb-4 text-center">
                                                        Send Valid (XRC20) Tokens otherwise you will lose your fund.
                                                    </p>
                                                    <div className='text-center'>
                                                        <QRCodeGenerator
                                                            tokenAddress={user.walletAddress}
                                                            // tokenAmount="100"
                                                            label="My ERC20 Tokens"
                                                            message="Thank you for your support"
                                                            size={300}
                                                        />
                                                    </div>

                                                </div>
                                            </div>

                                        </div>
                                    </div>{" "}
                                    {/* end card-body */}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>

        </div>
    )
}

export default ReceiveToken
